export default {
	data() {
		return {
			current: 0,

			starid: null,
			userRank: [],
			page: 1,
			myInfo: {},
			AVATAR:this.$app.AVATAR,
			NICKNAME: this.$app.NICKNAME,
		};
	},
	onLoad(option) {
		this.starid = option.starid
		this.loadData()
	},
	onReachBottom() {
		this.page++
		this.loadData()
	},
	methods: {
		switchAct(current) {
			this.page = 1
			this.current = current
			this.loadData()
		},
		loadData() {
			if (this.page > 10) return
			let field;
			if (this.current == 0) {
				field = 'thisday_count'
			} else if (this.current == 1) {
				field = 'thisweek_count'
			} else if (this.current == 2) {
				field = 'total_count'
			} else if (this.current == 3) {
				field = 'lastweek_count'
			}
			this.$app.request(this.$app.API.USER_RANK, {
				starid: this.starid,
				page: this.page,
				field,
			}, res => {
				this.myInfo = res.data.my

				if (this.page == 1) {
					this.userRank = res.data.list
				} else {
					this.userRank = this.userRank.concat(res.data.list)
				}
			})
		},
	}
}
